body {
  overscroll-behavior-y: contain;
}

.text-fredoka-one {
  font-family: "Fredoka One", "Roboto", "Helvetia Neue", Arial, Helvetica, sans-serif;
}

@media (max-width: 599px) {
  .small-screen-only {
    display: block;
  }
}
@media (min-width: 600px) {
  .small-screen-only {
    display: none;
  }
}

@media (max-width: 599px) {
  .large-screen-only {
    display: none;
  }
}
@media (min-width: 600px) {
  .large-screen-only {
    display: block;
  }
}

.constrain {
  max-width: 975px;
  margin: 0 auto;
}

.constrain-banner {
  max-width: 575px;
  margin: 0 auto;
}

.constrain-fw {
  margin-left: 20% !important;
  margin-right: 20% !important;
  width: 60% !important;
  margin-bottom: 20px;
}